.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    padding: 20px;
    background-color: #063970;
    color: rgb(138, 138, 138);
}
.search {
    padding: 10px;
    width: 300px;
    margin-left: 10px;
}
.profile-img {
    background-image: url(../../../assets/movies/profilePic.jpeg);
    background-size: cover;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.user-profile {
    display: flex;
    align-items: center;
}
.user-profile >*{
    margin: 0 10px;
}