.content {
    padding: 0 20px;
}
.content-sort {
    display: flex;
    height: 8vh;
    padding: 10px 0;
}
.dropdown-list {
    margin-left: 15px;
    width: 100px;
}
::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    background: #f1f1f1; 
}
::-webkit-scrollbar-thumb {
    background: #888; 
}
.content-movie {
    overflow-x: hidden;
    overflow-y: scroll;
    height: 80vh;
    justify-content: center;
}